<template>
  <div>
    <span v-for="(menu) in menus" :key="menu.id">
      <el-submenu :index="menu.id+''" v-if="menu.menuType === 0">
        <template slot="title">
          <i :class="menu.menuIcon"></i>
          <span slot="title">{{menu.menuTitle}}</span>
        </template>
        <div v-if="menu.children !== null && menu.children.length>0">
          <menuCOM :menus="menu.children" />
        </div>
      </el-submenu>

      <el-menu-item :index="menu.routerPath" v-if="menu.menuType === 1">
        <i :class="menu.menuIcon"></i>
        <span slot="title">{{ menu.menuTitle }}</span>
      </el-menu-item>

    </span>
  </div>
</template>

<script>
import menuCOM from './menuCOM'
export default {
  name: "menuCOM",
  props: ['menus'],
  components:{menuCOM},
  created() {
  }
}
</script>

